import PropTypes from 'prop-types';
import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import { useField } from 'formik';

import { LandingsPropTypes } from '../../domainTypes';
import { SelectPageInput } from '../Inputs';
import FieldRow from './FieldRow';

export default function SelectPageField({ website, onChange, ...rest }) {
    const { t } = useTranslation('ui');
    const [, { initialValue }, { setValue, setTouched }] = useField(rest.name);

    const handleChange = useCallback((value) => {
        setValue(value);
        setTouched(value !== initialValue);

        onChange?.(value);
    }, [initialValue, onChange]);

    return (
        <FieldRow {...rest}>
            {({ field: { name, value, disabled, ...rest } }) => (
                <SelectPageInput
                    {...rest}
                    placeholder={t('SelectPageField.placeholder')}
                    name={name}
                    disabled={disabled}
                    value={value}
                    website={website}
                    onChange={handleChange}
                    styles={{
                        control: (baseStyle) => ({
                            ...baseStyle,
                            border: 'none',
                            boxShadow: 'none',
                            borderWidth: 0,
                            borderRadius: 10,
                            height: 38,
                            padding: '0 0 0 16px',
                        }),
                        valueContainer: (baseStyle) => ({
                            ...baseStyle,
                            padding: 0,
                        }),
                        indicatorSeparator: (baseStyle) => ({
                            ...baseStyle,
                            display: 'none',
                        }),
                        indicatorsContainer: (baseStyle) => ({
                            ...baseStyle,
                            minWidth: 36,
                        }),
                        dropdownIndicator: (baseStyle) => ({
                            ...baseStyle,
                            width: 33,
                            padding: '0 10px',
                            color: '#333',
                        }),
                        menu: (baseStyle) => ({
                            ...baseStyle,
                            borderRadius: 10,
                            padding: 0,
                            overflow: 'hidden',
                        }),
                        clearIndicator: (baseStyle) => ({
                            ...baseStyle,
                            display: 'none',
                        }),
                        menuList: (baseStyle) => ({
                            ...baseStyle,
                            padding: 0,
                        }),
                    }}
                />
            )}
        </FieldRow>
    );
}

SelectPageField.propTypes = {
    name: PropTypes.string.isRequired,
    website: LandingsPropTypes.website.isRequired,
    onChange: PropTypes.func,
};
