import PropTypes from 'prop-types';
import React, { useCallback, useEffect } from 'react';
import { useFormikContext } from 'formik';
import { useTranslation } from 'react-i18next';
import { useUpdateEffect } from 'react-use';

import { normalizeRawUrl } from '@mssgme/helpers';
import { useFeature } from '@mssgme/hooks';
import { LandingsPropTypes } from '../../../domainTypes';
import SelectPageField from '../SelectPageField';
import TextField from '../TextField';
import SelectField from '../SelectField';
import AutosuggestStyle from './AutosuggestStyle';

const typeOptions = (t) => [
    {
        label: t('SmartLinkField.types.url'),
        value: 'url',
    },
    {
        label: t('SmartLinkField.types.page'),
        value: 'page',
    },
    {
        label: t('SmartLinkField.types.tel'),
        value: 'tel',
    },
    {
        label: t('SmartLinkField.types.email'),
        value: 'mailto',
    },
    {
        label: t('SmartLinkField.types.sms'),
        value: 'sms',
    },
];

const urlLabelsMap = (t) => ({
    url: {
        label: t('SmartLinkField.link.url.label'),
        placeholder: t('SmartLinkField.link.url.placeholder'),
    },
    page: {
        label: t('SmartLinkField.link.page.label'),
        placeholder: t('SmartLinkField.link.page.placeholder'),
    },
    tel: {
        label: t('SmartLinkField.link.tel.label'),
        placeholder: t('SmartLinkField.link.tel.placeholder'),
    },
    mailto: {
        label: t('SmartLinkField.link.email.label'),
        placeholder: t('SmartLinkField.link.email.placeholder'),
    },
    sms: {
        label: t('SmartLinkField.link.tel.label'),
        placeholder: t('SmartLinkField.link.tel.placeholder'),
    },
});

export default function SmartLinkField({ name, website, themeFields, required, urlLabel, titleLabel, ...rest }) {
    const { t } = useTranslation('ui');
    const { getFieldMeta, setFieldValue } = useFormikContext();
    const { value } = getFieldMeta(name);
    const applySuggestions = useFeature('link_auto_branding');
    const showPageSelector = value.action.type === 'page';
    const options = website?.pages.length > 1 ? typeOptions(t) : typeOptions(t).filter(({ value }) => value !== 'page');

    const handleNormalizeUrl = useCallback(() => {
        if (value.action.type === 'url') {
            const url = normalizeRawUrl(value.action.url || '');

            if (url && url !== value.action.url) {
                setFieldValue(`${name}.action.url`, url);
            }
        }
    }, [name, value.action.url, setFieldValue]);

    // fallback for old url (make migration before release?)
    useEffect(() => {
        if (value.url && !value.action.url && !value.action.page) {
            setFieldValue(`${name}.action.url`, value.url);
        }
    }, []);

    useUpdateEffect(() => {
        setFieldValue(`${name}.action.url`, '');
        setFieldValue(`${name}.action.page`, null);
    }, [value.action.type]);

    return (
        <div {...rest}>
            <TextField
                builderInput
                name={`${name}.title`}
                label={titleLabel || t('SmartLinkField.title.label')}
                placeholder={t('SmartLinkField.title.placeholder')}
            />

            <SelectField
                builderInput
                name={`${name}.action.type`}
                label={t('SmartLinkField.action')}
                options={options}
            />

            {!showPageSelector && (
                <TextField
                    builderInput
                    autoCapitalize="off"
                    style={{ marginBottom: 16 }}
                    required={required && !showPageSelector}
                    name={`${name}.action.url`}
                    label={urlLabel || urlLabelsMap(t)[value.action.type].label}
                    placeholder={urlLabelsMap(t)[value.action.type].placeholder}
                    onBlur={handleNormalizeUrl}
                />
            )}

            {applySuggestions && themeFields && <AutosuggestStyle link={value} themeFields={themeFields} />}

            {showPageSelector && (
                <SelectPageField
                    builderInput
                    website={website}
                    label={t('Page')}
                    name={`${name}.action.page`}
                    style={{ marginBottom: 16 }}
                />
            )}
        </div>
    );
}

SmartLinkField.propTypes = {
    name: PropTypes.string.isRequired,
    required: PropTypes.bool,
    website: LandingsPropTypes.website,
    urlLabel: LandingsPropTypes.inputLabel,
    titleLabel: LandingsPropTypes.inputLabel,
    themeFields: LandingsPropTypes.linkThemeFields,
};
