import { __t } from '../trans';
import { WEBSITE_DEFAULT_NAME } from '../constants';

let cache = null;

export default (page, isHomePage) => {
    const name = page.name;

    if (isHomePage) {
        if (!cache) {
            cache = Object.values(WEBSITE_DEFAULT_NAME);
        }

        if (!name || cache.includes(name)) {
            return __t('homePage');
        }
    }

    return name;
};
